@font-face {
  font-family: 'icoFont';
  src:  url('../fonts/icoFont/icoFont.eot');
  src:  url('../fonts/icoFont/icoFont.eot') format('embedded-opentype'),
  url('../fonts/icoFont/icoFont.ttf') format('truetype'),
  url('../fonts/icoFont/icoFont.woff') format('woff'),
  url('../fonts/icoFont/icoFont.svg') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icoFont' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: inherit;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-down:before {
  content: "\e900";
}
.icon-check:before {
  content: "\e901";
}
.icon-arrow:before {
  content: "\e902";
}
.icon-close:before {
  content: "\e903";
}
.icon-menu:before {
  content: "\e904";
}
.icon-whatsapp:before {
  content: "\e905";
}