$main-font-size: 16px;

@function strip-unit($number) {
  @if type-of($number) == "number" and not unitless($number) {
    @return $number / ($number * 0 + 1);
  }
  @return $number;
}

/// Convert value to em.
@function rem($value, $font-size: $main-font-size) {
  @return (strip-unit($value) / strip-unit($font-size)) + rem;
}

