$main-font-size: 16px;

@function strip-unit($number) {
  @if type-of($number) == "number" and not unitless($number) {
    @return $number / ($number * 0 + 1);
  }
  @return $number;
}

/// Convert value to em.
@function rem($value, $font-size: $main-font-size) {
  @return (strip-unit($value) / strip-unit($font-size)) + rem;
}

/*Ussage:
font-size:rem(32, 16); // => font-size:2rem;
*/

:root {
  --transition: 0.3s ease-in-out;
  --ploni: "Ploni 1.1 AAA", sans-serif;

  --domaine-sans-text: "Domaine Sans Text", sans-serif;
  --domaine-text: "Domaine Sans Text", sans-serif;
  --domaine-sans-display: "Domaine Sans Display", sans-serif;
  //Test Domaine Text

  --green: #00ff84;
  --pink: #ffc8c8;
  --blue: #5200ff;
  --dark-blue: #3c00bb;
  --red: #ff9aa2;
  --light-grey: #f9f9f9;
  --dark-grey: #afafaf;
  --crimson: #f01729;
  --black: #333333;
  --white: #ffffff;
}

html {
  font-size: $main-font-size;
  overflow-x: hidden;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  position: relative;
  min-width: 320px;
  color: var(--dark-blue);
  font-family: var(--ploni);

  -webkit-font-smoothing: subpixel-antialiased;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  backface-visibility: hidden;
  text-rendering: geometricPrecision;
}

* {
  outline: none;
  word-wrap: break-word;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
a:focus,
a:hover {
  text-decoration: none;
}

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  border: 0;
  max-width: 100%;
  vertical-align: middle;
}

button {
  padding: 0;
  cursor: pointer;
}

input,
button,
textarea {
  border-radius: 0;
  -webkit-appearance: none;
}

select {
  -webkit-appearance: none;
}

.container {
  width: 100%;
  max-width: rem(1078);
  margin: 0 auto;
  padding: 0 rem(15);
}

.main-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: rem(56);
  padding: rem(10) rem(21);
  font-size: rem(18);
  font-weight: 500;
  line-height: 1;
  color: var(--white);
  border: 1px solid var(--blue);
  background-color: var(--blue);
  box-shadow: rem(2) rem(2) rem(35) rgba(0, 0, 0, 0.07);
  border-radius: rem(12);
  transition: color var(--transition), border-color var(--transition),
    background-color var(--transition);

  &.pink {
    color: var(--blue);
    background-color: var(--pink);
    border-color: var(--pink);
  }

  &.outline {
    color: var(--pink);
    background-color: transparent;
    border-color: var(--pink);
  }

  &:hover {
    color: var(--blue);
    border-color: var(--green);
    background-color: var(--green);
  }
}

.instagram-icon,
.whatsapp-icon,
.login-icon,
.user-icon {
  width: rem(25);
  height: rem(25);
  display: block;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.instagram-icon {
  background-image: url(data:image/svg+xml;base64,);
}

.whatsapp-icon {
  background-image: url(data:image/svg+xml;base64,);
}

.login-icon {
  background-image: url(data:image/png+xml;base64,);
}

.user-icon {
  background-image: url(data:image/png+xml;base64,);
}

.contact-us {
  right: 4.4%;
  bottom: 6%;
  position: fixed;
  z-index: 50;

  a {
    width: rem(91);
    height: rem(91);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: rem(8);
    font-family: var(--domaine-sans-display);
    font-weight: 600;
    font-size: rem(11);
    line-height: 1;
    color: var(--blue);
    text-align: center;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    background-image: url(data:image/svg+xml;base64,);
    transform: rotate(6deg);

    span {
      display: block;
      margin-bottom: rem(-3);
      font-size: rem(37);
    }
  }
}

header {
  padding: rem(18) 0;
  background-color: var(--blue);

  .container {
    max-width: rem(1390);
  }

  .data-wrap {
    position: relative;
    z-index: 5;

    .main-menu {
      z-index: -1;
    }
  }

  .toolbar-wrap {
    display: flex;
    align-items: center;

    &.login-type {
      .links-box:last-child,
      .logo {
        display: none;
      }
    }
  }

  .logo {
    width: rem(308);
    padding: 0 rem(10);
    text-align: center;

    a {
      display: block;
      max-width: rem(92);
      margin: 0 auto;
      text-align: center;
    }

    &.long {
      a {
        max-width: rem(183);
      }
    }
  }

  .links-box {
    width: calc(50% - #{rem(154)});
    display: flex;

    a {
      display: inline-flex;
      padding: 0 rem(15);
      align-items: center;
      font-family: var(--domaine-sans-text);
      font-weight: 300;
      font-size: rem(16);
      line-height: 1;
      letter-spacing: -0.065em;
      color: var(--white);
      border-left: 2px solid var(--white);
      transition: color var(--transition);

      b {
        font-weight: 500;
      }

      .login-icon,
      .user-icon {
        margin-right: rem(12);
      }

      &:hover {
        color: var(--green);
      }

      &:first-child {
        padding-right: 0;
      }

      &:last-child {
        border: none;
        padding-left: 0;
      }
    }

    &:last-child {
      flex-direction: row-reverse;

      a {
        padding: 0 rem(19);
        border-left: none;
        border-right: 2px solid var(--white);

        &:first-child {
          padding-left: 0;
        }

        &:last-child {
          border: none;
          padding-right: 0;
        }
      }
    }
  }

  .open-menu {
    display: none;
    width: rem(22);

    button {
      display: flex;
      font-size: rem(22);
      color: var(--white);
      background: none;
      border: none;

      span {
        pointer-events: none;
      }

      &.active {
        .icon-menu:before {
          content: "\e903";
        }
      }
    }
  }
}

.main-menu {
  display: flex;
  padding: rem(118) 0 rem(20) 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--blue);
  overflow: hidden;

  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity var(--transition), visibility var(--transition);

  .scroll-wrap {
    flex: auto;
    display: flex;
    flex-direction: column;
    margin-right: rem(-10);
    padding-right: rem(10);
    overflow: auto;

    &::-webkit-scrollbar {
      width: 5px;
    }

    /* Track */
    &::-webkit-scrollbar-track {
      background: transparent;
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
      background: var(--dark-blue);
    }

    /* Handle on hover */
    &::-webkit-scrollbar-thumb:hover {
      background: var(--green);
    }

    .menu-wrap {
      flex: auto;
    }

    .menu-item {
      ul {
        li {
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 0 rem(38);
          position: relative;
          z-index: 1;

          a {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: rem(22) 0;
            border-bottom: 1px solid #61fd7d;

            &:before {
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              right: 0;
              bottom: 0;
              z-index: -1;
              opacity: 0;
              background: #ffd6ea;
              transition: opacity var(--transition);
            }

            &:hover {
              border-color: #ffd6ea;

              &:before {
                opacity: 1;
              }

              span {
                &.name {
                  color: var(--blue);
                }

                &.label {
                  color: #ffd6ea;
                  background: var(--blue) !important;
                }
              }
            }
          }

          span {
            display: inline-block;

            &.name {
              padding-left: rem(20);
              font-weight: bold;
              font-size: rem(18);
              line-height: 1.09;
              letter-spacing: -0.005em;
              color: var(--green);
              transition: color var(--transition);
            }

            &.label {
              padding: rem(3) rem(8);
              font-weight: bold;
              font-size: rem(10);
              line-height: 1.09;
              letter-spacing: -0.005em;
              background: #ffd6ea;
              border-radius: rem(8);
              color: var(--blue);
              transition: color var(--transition), background var(--transition);
            }
          }
        }
      }
    }

    .extra-links {
      padding-top: rem(43);

      ul {
        padding: 0 rem(38);

        li {
          padding: rem(13) 0;
          font-weight: bold;
          font-size: rem(16);
          line-height: 1.09;
          letter-spacing: -0.005em;
          color: var(--green);

          a {
            color: inherit;
            transition: color var(--transition);

            &:hover {
              color: #ffd6ea;
            }
          }
        }
      }
    }

    .info-box {
      padding: rem(25) rem(38) 0 rem(38);

      .contacts-box {
        ul {
          li {
            padding: rem(4) 0;
            font-weight: 500;
            font-size: rem(16);
            line-height: 1.35;
            letter-spacing: -0.01em;
            color: var(--white);

            a {
              color: inherit;
              transition: color var(--transition);

              &:hover {
                color: #ffd6ea;
              }
            }
          }
        }
      }

      .social-box {
        padding-top: rem(18);

        ul {
          display: flex;
          flex-wrap: wrap;

          li {
            padding: rem(13) 0 0 rem(20);

            a {
              display: block;

              span {
                width: rem(49);
                height: rem(49);
              }
            }
          }
        }
      }
    }
  }
}

