      font-size: rem(32);
      line-height: 1.09;
      text-align: center;
      letter-spacing: -0.005em;
    }
  }

  ul {
    li {
      padding: rem(14) rem(42);
      margin-bottom: rem(16);
      font-size: rem(18);
      line-height: 1.33;
      position: relative;
      z-index: 1;
      border: 1px solid var(--blue);
      border-radius: rem(12);
      overflow: hidden;

      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;
        background-color: var(--blue);
        opacity: 0;
        pointer-events: none;
        transition: opacity var(--transition);
      }

      .question {
        position: relative;
        cursor: pointer;

        &:before {
          content: "\e900";
          font-family: "icoFont" !important;
          font-size: rem(24);
          position: absolute;
          top: rem(-4);
          right: rem(-27);
          transform: rotate(90deg);
          transition: transform var(--transition);
        }

        h4 {
          margin: 0;
          font-weight: 500;
          font-size: inherit;
        }
      }

      .answer {
        display: none;

        p {
          margin: 0;
          font-weight: normal;
        }
      }

      &.open {
        &:before {
          opacity: 0.1;
        }

        .question {
          position: relative;
          cursor: pointer;

          &:before {
            transform: rotate(0);
          }
        }

        .answer {
          display: block;
        }
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

