.form-section {
  padding-top: rem(11);
  padding-bottom: rem(29);

  h3 {
    margin: 0;
    padding: 0 rem(20);
    font-family: var(--domaine-sans-text);
    font-weight: normal;
    font-size: rem(18);
    line-height: 1.09;
    text-align: center;
    letter-spacing: -0.065em;
    position: relative;
    z-index: 1;

    &:before {
      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      left: 0;
      z-index: -1;
      border-top: 1px solid var(--dark-blue);
