.form-fields {
  display: flex;
  flex-wrap: wrap;

  label {
    display: flex;
    flex-direction: column;
    margin: 0 rem(20);
    padding-bottom: rem(24);
    position: relative;

    &.full-size {
      width: calc(100% - #{rem(40)});
    }

    &.half-size {
      width: calc(100% / 2 - #{rem(40)});
    }

    &.third-size {
      width: calc(100% / 3 - #{rem(40)});
    }

    &.quarter-size {
      width: calc(100% / 4 - #{rem(40)});
    }

    span {
      display: block;
      color: var(--blue);
      line-height: 1;

      &.warning {
        font-weight: 500;
        font-size: rem(12);
        line-height: 1.5;
        letter-spacing: rem(0.2);
        color: var(--crimson);
        position: absolute;
        left: 0;
        right: 0;
        bottom: rem(7);
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        pointer-events: none;
        opacity: 0;
        visibility: hidden;
        transition: opacity var(--transition), visibility var(--transition);
      }

      &.field {
        position: relative;
        z-index: 1;

        &.select {
          &:after {
            font-family: "icoFont";
            content: "\e900";
            display: flex;
            align-items: center;
            font-size: rem(24);
            position: absolute;
            top: 0;
            left: rem(17);
            bottom: 0;
            z-index: 5;
            transition: color var(--transition), transform var(--transition);
          }
        }
      }
