            padding-right: rem(15);

            p {
              margin: 0;
              padding-bottom: rem(14);
              font-weight: 700;
              font-size: rem(18);
              line-height: 1.13;
              letter-spacing: -0.01em;
              color: var(--dark-blue);
              text-align: left;

              &:last-child {
                padding-bottom: 0;
              }

              span {
                display: inline-block;
                padding: rem(7) rem(16);
                font-weight: normal;
                font-size: rem(16);
                background: rgba(255, 200, 200, 0.3);

                b {
                  font-weight: 700;
                  font-size: rem(20);
                }
              }
            }
          }

          .text {
            grid-area: 2 / 1 / 3 / 2;

            h5 {
              margin: 0;
              padding-bottom: rem(16);
              font-weight: normal;
              font-size: rem(16);
              line-height: 1.13;
              letter-spacing: -0.01em;
              b {
                font-weight: 700;
              }
            }

            p {
              margin: 0 0 0 rem(-45);
              font-weight: normal;
              font-size: rem(16);
              line-height: 1.4;
              letter-spacing: -0.01em;
              b {
                font-weight: 700;
              }
            }
          }
        }

        .btn-wrap {
          padding-top: rem(20);

          .main-btn {
            width: 100%;
            min-height: rem(35);
            justify-content: flex-start;
            font-weight: normal;
            font-size: rem(16);

            color: var(--blue);
            border-color: var(--blue);

            &:hover {
              color: var(--blue);
              border-color: var(--green);
              background-color: var(--green);
            }
          }
        }
      }
    }
  }

  .summary-box {
    max-width: rem(423);
    margin: 0 auto 0 0;
    padding-top: rem(30);

    .total-info {
      padding-bottom: rem(24);

      ul {
        li {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-bottom: rem(5);
          font-weight: normal;
          font-size: rem(20);
          line-height: 1.13;
          letter-spacing: -0.01em;
          background: rgba(255, 200, 200, 0.3);

          span {
            display: block;
            padding: rem(4) rem(7);
          }

          &.result {
            font-weight: bold;
            // font-size: rem(27);
            background: var(--pink);
          }
        }
      }
    }

    .agree-box {
      padding-bottom: rem(20);
    }

    .btn-wrap {
      padding-bottom: rem(20);

      .main-btn {
        width: 100%;
        max-width: rem(241);
        font-weight: 500;
        font-size: rem(18);
      }
    }

    .text-box {
      p {
        margin: 0;
        font-weight: normal;
        font-size: rem(16);
        line-height: 1.4;
        letter-spacing: -0.01em;
      }
    }
  }
}

.modals-wrap {
  display: none;
}

.modal-content {
  width: 100%;
  max-width: rem(716);
  max-height: 100%;
  padding: rem(60) rem(36);
  color: var(--white);
  background: var(--blue);
  border-radius: rem(10);

  .carousel__button.is-close {
    display: none;
  }

  .modal-scroll {
    overflow: auto;

    &::-webkit-scrollbar {
      width: 5px;
    }

    /* Track */
    &::-webkit-scrollbar-track {
      background: transparent;
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
      background: var(--dark-blue);
    }

    /* Handle on hover */
    &::-webkit-scrollbar-thumb:hover {
      background: var(--green);
    }
  }

  .data-wrap {
    max-width: rem(430);
    margin: 0 auto;

    .modal-title {
      h3 {
        margin: 0;
        font-family: var(--domaine-sans-display);
        font-weight: bold;
        font-size: rem(55);
        line-height: 1.09;
        text-align: center;
        letter-spacing: -0.045em;
      }

      p {
        margin: 0;
        padding-top: rem(10);
        font-weight: normal;
        font-size: rem(18);
        line-height: 1.35;
        text-align: center;
        letter-spacing: -0.01em;
      }
    }

    .btn-wrap {
      padding-top: rem(38);
      text-align: center;
    }
  }
}
